
.file {
	background-color:#fff;
    position: relative;
    display: inline-block;
    background-image: url('../../assets/common/images/bigadd.png') ;
    background-size: 100px,100px;
    border: 1px solid #99D3F5;
    border-radius: 4px;
    padding: 4px 12px;
    overflow: hidden;
    color: #1E88C7;
    text-decoration: none;
    text-indent: 0;
    width: 80px;
    height:80px;
	 background-repeat:no-repeat;
}
.file input {
    position: absolute;
    font-size: 100px;
    right: 0;
    top: 0;
    opacity: 0;
}
.file:hover {
    opacity: 0.6
}



